import {AfterViewInit, ChangeDetectorRef, Component, OnDestroy, OnInit} from '@angular/core';
import {Subscription} from "rxjs/Subscription";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {ServerService} from "../../../../services/server-service";
import {Http} from "@angular/http";
import {ActivatedRoute, Router} from "@angular/router";
import {JobService} from "../../../../services/job.service";
import {NgbModal} from "@ng-bootstrap/ng-bootstrap";

@Component({
  selector: 'app-job-comments',
  templateUrl: './job-comments.component.html',
  styleUrls: ['./job-comments.component.scss']
})
export class JobCommentsComponent implements OnInit, OnDestroy, AfterViewInit {
  userComments: any = null;
  newComment: boolean = false;
  userCommentInput: string;
  paramsReceived: any;
  jobServiceSubscription: Subscription;
  path: string = null;
  commentForm: FormGroup;
  partyId: string = null;

  constructor(private serverService: ServerService, private http: Http,
              private activatedRoute: ActivatedRoute, private changeDetectorRef: ChangeDetectorRef,
              private  router: Router, private jobService: JobService,
              private ngbModal: NgbModal) {
  }

  ngOnInit() {
    this.jobServiceSubscription = this.jobService.jobSourceObservable
      .subscribe((data) => {
        if (data && data.id && data.type === JobService.TYPE_COMMENTS) {
          console.log(data);
          this.paramsReceived = data.id;
          this.path = "http://localhost:8080/api/jobs/" + this.paramsReceived + "/comments";
          this.getUserComments();
          this.getPartyId();
        }
      });
  }

  ngAfterViewInit() {
    this.jobService.announceParentComponent({type: JobService.TYPE_COMMENTS});
  }


  getUserComments() {
    this.serverService.getRequestByPath(this.path)
      .subscribe(
        (comments: any) => {
          console.log(comments);
          this.userComments = comments._embedded.comments;
          this.formatDate();
        },
        (error) => {
          console.log(error);
        });
  }

  getPartyId() {
    this.serverService.getRequestByJobId(this.paramsReceived)
      .subscribe((response) => {
        this.partyId = response.customerUid;
        console.log(response);
      }, (error) => {
        console.log(error);
      })
  }

  formatDate() {
    this.userComments.forEach((data) => {
      let date = data.commentDtm;
      let formattedDate = new Date(date);
      data.commentDtm = formattedDate.toDateString();
    })
  }


  createNewComment() {
    console.log("create new comment");
    this.createCommentForm();
    this.newComment = true;
  }

  createCommentForm() {
    this.commentForm = new FormGroup({
      'user-comment': new FormControl(null, [Validators.required, this.validateComment.bind(this)])
    });
  }

  validateComment() {
    console.log(this.commentForm);
    if (this.commentForm && this.commentForm.get('user-comment')
      && this.commentForm.get('user-comment').value
      && this.commentForm.get('user-comment').value.replace(/^\s+$/, '') != "") {
      return null;
    }
    else return {'Invalid Comment': true}
  }

  saveUserComment() {
    if (this.commentForm && this.commentForm.valid) {
      let comment = {
        "comment": this.commentForm.get('user-comment').value,
        "staffUser": '/staffs/' + "motion6",
        "job": "/jobs/" + this.paramsReceived,
      };
      this.serverService.saveUserComment(comment)
        .subscribe((response) => {
          console.log(response);
          this.ngbModal.open("Comments Succesfull saved");
          this.getUserComments();
          this.dontSaveUserComment();
        }, (error) => {
          console.log(error);
          this.ngbModal.open("Sorry, something went wrong. !");
        });
    }
    else {
      this.ngbModal.open("Please enter a valid comment");
    }
  }

  dontSaveUserComment() {
    this.commentForm.reset();
    this.newComment = false;
  }

  usersCommentFileUpload($event) {
    console.log("usersCommentFileUpload called");
  }

  onSubmit() {
    if (this.commentForm.valid) {
      this.saveUserComment();
    }
    else {
      this.ngbModal.open("Please enter a valid comment");
    }
  }

  ngOnDestroy() {

  }
}



