import {AfterViewInit, Component, OnInit, ViewEncapsulation} from '@angular/core';
import {jobTabs} from "./job-tabs";
import {ActivatedRoute} from "@angular/router";
import {JobService} from "../../../services/job.service";
import {ServerService} from "../../../services/server-service";
import {Subscription} from "rxjs/Subscription";
import {AppInteractionService} from "../../../services/app-interaction.service";


@Component({
  selector: 'app-job',
  templateUrl: './job.component.html',
  styleUrls: ['./job.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers:[ JobService]
})
export class JobComponent implements OnInit, AfterViewInit {

  jobTabs = jobTabs;
  paramsReceived: string;
  childComponentSubscription:Subscription;
  appServiceSubscription:Subscription;
  customerUuid:string = null;
  jobType:string = null;

  constructor(private serverService: ServerService, private activatedRoute: ActivatedRoute,
              private jobService: JobService, private appInteractionService: AppInteractionService,) {
  }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params) => {
      console.log(params);
      this.paramsReceived = params['id'];
      if(params['id'] === AppInteractionService.SERVICE_REQUEST
        || params['id'] === AppInteractionService.QUOTE_REQUEST) {
        this.paramsReceived = null;
      }
    }, (error) => {
      console.log(error)
    });
    this.childComponentSubscription = this.jobService.childComponentObservable
      .subscribe((data)=>{
        if (data.type === JobService.TYPE_MAIN) {
          this.announceToMain();
        }
        if (data.type === JobService.TYPE_COMMENTS) {
          this.announceToComments();
        }
        if (data.type === JobService.TYPE_SCHEDULES) {
          this.announceToSchedules();
        }
        if (data.type === JobService.TYPE_FIELDWORKS) {
          this.announceToFieldWorks();
        }
        if (data.type === JobService.TYPE_BILLS) {
          this.announceToBills();
        }
        if (data.type === JobService.TYPE_INVOICES) {
          this.announceToInvoices();
        }
      }, (error) => {console.log(error);});
    this.appServiceSubscription = this.appInteractionService.appSourceObservable
      .subscribe((data) => {
        console.log(data);
        if (data && data.customerUuid && data.jobType) {
          this.customerUuid = data.customerUuid;
          this.jobType = data.jobType;
          this.paramsReceived = null;
          this.announceToMain();
        }
      });
  }

  ngAfterViewInit() {
    this.appInteractionService.requestApplicationComponent(
      {customerUuid:this.customerUuid,
        jobType: this.jobType,type:AppInteractionService.QUOTE_REQUEST});
  }

  announceToMain() {
    console.log(this.customerUuid);
    this.jobService.announceJobDetail({id: this.paramsReceived, type: JobService.TYPE_MAIN,
      customerUuid:this.customerUuid, jobType:this.jobType});
  }

  announceToComments() {
    this.jobService.announceJobDetail({id:this.paramsReceived, type:JobService.TYPE_COMMENTS});
  }

  announceToSchedules() {
    this.jobService.announceJobDetail({id:this.paramsReceived, type:JobService.TYPE_SCHEDULES});
  }

  announceToFieldWorks() {
    this.jobService.announceJobDetail({id:this.paramsReceived, type:JobService.TYPE_FIELDWORKS});
  }
  announceToBills() {
    this.jobService.announceJobDetail({id:this.paramsReceived, type:JobService.TYPE_BILLS});
  }

  announceToInvoices() {
    this.jobService.announceJobDetail({id:this.paramsReceived, type:JobService.TYPE_INVOICES});
  }
}



