import {ChangeDetectorRef, Component, ViewEncapsulation} from '@angular/core';
import {Subject} from "rxjs/Subject";
import {NavBarService} from "./services/nav-bar.service";
import {state, style, trigger} from "@angular/animations";
import {AppInteractionService} from "./services/app-interaction.service";
import {ActivatedRoute, Router} from "@angular/router";
import {ServerService} from "./services/server-service";
import {Subscription} from "rxjs/Subscription";
import {Location} from "@angular/common";


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [NavBarService, AppInteractionService],
  animations: [
    trigger('app-sidebar', [
      state('expanded', style({
        left: '200px'
      })),
      state('collapsed', style({
        left: '0'
      }))
    ])
  ],
})

export class AppComponent {
  title = 'Jobowit';
  state = 'expanded';
  public isCollapsed = false;
  navBarSubject: Subject<any>;
  childComponentSubscription: Subscription;
  customerUuid = null;
  jobType = null;

  constructor(private navBarService: NavBarService,
              private changeDetectorRef: ChangeDetectorRef,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              private serverService: ServerService,
              private location: Location,
              private appInteractionService: AppInteractionService) {

  }

  ngOnInit() {
    this.navBarSubject = this.navBarService.getNavBarSubject();
    this.activatedRoute.params.subscribe((params) => {
      console.log(params);
    }, (error) => console.log(error));
    this.childComponentSubscription = this.appInteractionService.childComponentObservable
      .subscribe((data) => {
        if (data && data.customerUuid && (data.jobType === AppInteractionService.QUOTE_REQUEST ||
            data.jobType === AppInteractionService.SERVICE_REQUEST)) {
          this.jobType = data.jobType;
          this.customerUuid = data.customerUuid;
          this.appInteractionService.announceRequestDetail({
            customerUuid: data.customerUuid,
            jobType: data.jobType,
            type: AppInteractionService.TYPE_MAIN
          });
        }
      });
  }

  ngAfterViewInit() {

  }

  toggleCollapse() {
    if (this.state == 'expanded') {
      this.state = 'collapsed';

    }
    else {
      this.state = 'expanded';
      this.changeDetectorRef.detectChanges();
    }
  }

  onTransitionEnd($event) {
    this.navBarSubject.next();
  }

}



